'use client';

import React, { useEffect, useState } from 'react';
import { Card } from '../../../components/UI';
import { Users, Building2, TrendingUp, Globe, Mail, Briefcase, UserCheck, MapPin } from 'lucide-react';
import { useUser } from '@clerk/nextjs';

const StatCard = ({ title, value, change, changeType, icon: Icon, subtext }: any) => {
  const isPositive = changeType === 'positive';

  return (
    <Card className="p-6 hover:shadow-lg transition-shadow duration-200">
      <div className="flex justify-between items-start mb-4">
        <div className="flex-1">
          <span className="text-sm text-gray-500 dark:text-gray-400 font-medium">{title}</span>
        </div>
        <div className="p-2.5 rounded-lg bg-blue-50 dark:bg-blue-900/20">
          <Icon size={20} className="text-blue-600 dark:text-blue-400" />
        </div>
      </div>

      <div>
        <h3 className="text-3xl font-bold text-gray-900 dark:text-white mb-2">{value}</h3>
        <div className="flex items-center text-sm gap-2">
          {change && (
            <span className={`font-medium ${isPositive ? 'text-green-600 dark:text-green-400' : 'text-gray-500'}`}>
              {change}
            </span>
          )}
          <span className="text-gray-500 dark:text-gray-400">{subtext}</span>
        </div>
      </div>
    </Card>
  );
};

interface DashboardStats {
  contacts: number;
  agencies: number;
  usage: {
    count: number;
    total: number;
    remaining: number;
  };
}

interface Stat {
  name: string;
  count: number;
  percentage: number;
}

export default function Dashboard() {
  const { user } = useUser();
  const [stats, setStats] = useState<DashboardStats | null>(null);
  const [domainStats, setDomainStats] = useState<Stat[]>([]);
  const [jobTitleStats, setJobTitleStats] = useState<Stat[]>([]);
  const [departmentStats, setDepartmentStats] = useState<Stat[]>([]);
  const [agencyStats, setAgencyStats] = useState<Stat[]>([]);
  const [loading, setLoading] = useState(true);
  const [errorMsg, setErrorMsg] = useState<string>('');

  useEffect(() => {
    const loadDashboardData = async () => {
      try {
        const response = await fetch('/api/dashboard/stats');

        if (!response.ok) {
          const text = await response.text();
          console.error('Dashboard stats response error:', response.status, text);
          throw new Error(`Failed to fetch dashboard stats: ${response.status}`);
        }

        const data: DashboardStats = await response.json();
        setStats(data);

        // Fetch contacts to calculate statistics
        const contactsResponse = await fetch('/api/contacts?limit=1000');
        if (contactsResponse.ok) {
          const contactsData = await contactsResponse.json();
          const contacts = contactsData.contacts || [];

          // Calculate domain statistics
          const domainMap = new Map<string, number>();
          const jobTitleMap = new Map<string, number>();
          const departmentMap = new Map<string, number>();
          const agencyMap = new Map<string, number>();

          contacts.forEach((contact: any) => {
            // Email domains
            if (contact.email) {
              const domain = contact.email.split('@')[1] || 'Unknown';
              domainMap.set(domain, (domainMap.get(domain) || 0) + 1);
            }

            // Job titles
            if (contact.jobTitle) {
              jobTitleMap.set(contact.jobTitle, (jobTitleMap.get(contact.jobTitle) || 0) + 1);
            }

            // Departments
            if (contact.department) {
              departmentMap.set(contact.department, (departmentMap.get(contact.department) || 0) + 1);
            }

            // Agencies
            if (contact.agency?.name) {
              agencyMap.set(contact.agency.name, (agencyMap.get(contact.agency.name) || 0) + 1);
            }
          });

          const total = contacts.length;

          const createStats = (map: Map<string, number>) =>
            Array.from(map.entries())
              .map(([name, count]) => ({
                name,
                count,
                percentage: Math.round((count / total) * 100)
              }))
              .sort((a, b) => b.count - a.count)
              .slice(0, 5);

          setDomainStats(createStats(domainMap));
          setJobTitleStats(createStats(jobTitleMap));
          setDepartmentStats(createStats(departmentMap));
          setAgencyStats(createStats(agencyMap));
        }
      } catch (error) {
        console.error('Error loading dashboard data:', error);
        setErrorMsg((error as Error)?.message || 'Unknown error');
      } finally {
        setLoading(false);
      }
    };

    loadDashboardData();
  }, []);

  const usagePercentage = stats ? Math.min(100, Math.round((stats.usage.count / stats.usage.total) * 100)) : 0;
  const isLimitReached = stats && stats.usage.count >= stats.usage.total;

  const colors = ['#3B82F6', '#8B5CF6', '#EC4899', '#F59E0B', '#10B981'];

  const ChartCard = ({ title, icon: Icon, data, emptyMessage }: { title: string, icon: any, data: Stat[], emptyMessage: string }) => (
    <Card className="p-6">
      <div className="flex items-center gap-3 mb-6">
        <div className="p-2.5 rounded-lg bg-blue-50 dark:bg-blue-900/20">
          <Icon className="text-blue-600 dark:text-blue-400" size={20} />
        </div>
        <div>
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white">{title}</h3>
          <p className="text-sm text-gray-500 dark:text-gray-400">Top 5 distribution</p>
        </div>
      </div>

      {loading ? (
        <div className="flex items-center justify-center h-48">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
        </div>
      ) : data.length > 0 ? (
        <div className="space-y-3">
          {data.map((stat, index) => (
            <div key={stat.name} className="space-y-2">
              <div className="flex justify-between items-center">
                <div className="flex items-center gap-2">
                  <div
                    className="w-3 h-3 rounded-full"
                    style={{ backgroundColor: colors[index % colors.length] }}
                  />
                  <span className="text-sm font-medium text-gray-700 dark:text-gray-300 truncate">
                    {stat.name}
                  </span>
                </div>
                <span className="text-sm text-gray-500 dark:text-gray-400 whitespace-nowrap ml-2">
                  {stat.count} ({stat.percentage}%)
                </span>
              </div>
              <div className="relative h-2 rounded-full bg-gray-200 dark:bg-gray-700 overflow-hidden">
                <div
                  style={{
                    width: `${stat.percentage}%`,
                    backgroundColor: colors[index % colors.length]
                  }}
                  className="h-full transition-all duration-500"
                />
              </div>
            </div>
          ))}
        </div>
      ) : (
        <div className="flex flex-col items-center justify-center h-48 text-gray-400">
          <Icon size={48} className="mb-2 opacity-50" />
          <p className="text-sm">{emptyMessage}</p>
        </div>
      )}
    </Card>
  );

  return (
    <div className="space-y-6">
      {/* Header */}
      <div>
        <h1 className="text-2xl font-bold text-gray-900 dark:text-white mb-1">
          Dashboard Overview
        </h1>
        <p className="text-gray-500 dark:text-gray-400">
          Welcome back, {user?.firstName || 'User'}! Here's your analytics summary.
        </p>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-6">
        <StatCard
          title="Tracked Agencies"
          value={loading || !stats ? "..." : stats.agencies}
          change="+4"
          changeType="positive"
          subtext="new regions"
          icon={Building2}
        />
        <StatCard
          title="Total Contacts"
          value={loading || !stats ? "..." : stats.contacts.toLocaleString()}
          change="+150"
          changeType="positive"
          subtext="available leads"
          icon={Users}
        />
        <StatCard
          title="Daily API Usage"
          value={loading || !stats ? "..." : `${stats.usage.count}/${stats.usage.total}`}
          change="Resets Daily"
          changeType="neutral"
          subtext="view limit"
          icon={TrendingUp}
        />
      </div>

      {errorMsg && (
        <div className="p-4 rounded-lg bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 text-sm text-red-700 dark:text-red-400">
          <strong className="font-semibold">Error:</strong> {errorMsg}
        </div>
      )}

      {/* Usage Progress */}
      <Card className="p-6">
        <div className="flex items-center gap-3 mb-6">
          <div className="p-2.5 rounded-lg bg-blue-50 dark:bg-blue-900/20">
            <TrendingUp className="text-blue-600 dark:text-blue-400" size={20} />
          </div>
          <div>
            <h3 className="text-lg font-semibold text-gray-900 dark:text-white">Daily Credits</h3>
            <p className="text-sm text-gray-500 dark:text-gray-400">Resets at midnight</p>
          </div>
        </div>

        <div className="space-y-4">
          <div className="flex justify-between items-center">
            <span className="text-sm text-gray-600 dark:text-gray-400">
              {stats ? stats.usage.count : 0} of {stats ? stats.usage.total : 50} used
            </span>
            <span className={`text-lg font-bold ${isLimitReached ? 'text-red-600' : 'text-blue-600'}`}>
              {usagePercentage}%
            </span>
          </div>

          <div className="relative h-3 rounded-full bg-gray-200 dark:bg-gray-700 overflow-hidden">
            <div
              style={{ width: `${usagePercentage}%` }}
              className={`h-full transition-all duration-500 ${isLimitReached ? 'bg-red-500' : 'bg-blue-600'
                }`}
            />
          </div>

          <p className="text-sm text-gray-600 dark:text-gray-400">
            {!stats ? 'Loading...' : isLimitReached
              ? "You've reached your daily limit. Upgrade or come back tomorrow!"
              : `${stats.usage.remaining} credits remaining today.`}
          </p>
        </div>
      </Card>

      {/* Analytics Charts Grid */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <ChartCard
          title="Top Email Domains"
          icon={Globe}
          data={domainStats}
          emptyMessage="No email data available"
        />
        <ChartCard
          title="Top Job Titles"
          icon={Briefcase}
          data={jobTitleStats}
          emptyMessage="No job title data available"
        />
        <ChartCard
          title="Top Departments"
          icon={UserCheck}
          data={departmentStats}
          emptyMessage="No department data available"
        />
        <ChartCard
          title="Top Agencies"
          icon={MapPin}
          data={agencyStats}
          emptyMessage="No agency data available"
        />
      </div>
    </div>
  );
}